@import url(https://fonts.googleapis.com/css2?family=Zen+Dots&display=swap);
.videoContainer {
  /* height: 300px;
  width: 300px; */
  position: relative;
}

.videoContainer video {
  width: 100%;
  height: 100%;
  position: absolute;
  object-fit: cover;
  z-index: -1;
}

h1,
p {
  color: white;
}

.makeStyles-root-5 {
  background-image: url("https://images.pexels.com/photos/2255355/pexels-photo-2255355.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940");
  background-position: center, center;
  background-repeat: no-repeat, no-repeat;
  padding: 25px;
  background-size: cover;
}

.App {
  text-align: center;
  margin: 0px;
  padding: 0%;
}

/* @import url("https://fonts.googleapis.com/css2?family=Zen+Dots&display=swap"); */

h1.MuiTypography-root {
  font-family: "Zen Dots", cursive;
}
p.MuiTypography-root {
  font-family: "Roboto", sans-serif;
}

h2,
h5,
span,
h3,
button,
p {
  font-family: "Roboto", sans-serif !important;
}

@media (max-width: 670px) {
  h2,
  h5,
  span,
  h3,
  button,
  p {
    font-size: smaller;
  }
  h5.MuiTypography-root {
    font-size: 0.6rem;
  }
  h3.makeStyles-heading-17 {
    font-size: 1.5rem;
  }
  img.makeStyles-logo-4 {
    width: 70px;
    height: auto;
  }
  p.email {
    display: none;
  }
  span.makeStyles-para-18 {
    font-size: small;
  }
}

